<template>
    <div>
        <el-dialog :visible.sync="dialogVisible" width="30%">
            <p><span>商品货号：{{  }}</span></p>
            <el-table ref="skuTable" :data="skuData" style="width: 100%" v-loading="skuLoad" border >
                <el-table-column label="SKU编号" width="60" align="center"> 
                    <template slot-scope="scope"><el-input v-model="scope.row.sku_ID"></el-input></template>
                </el-table-column>
                <el-table-column label="颜色" width="60" align="center" prop="sku_color"></el-table-column>
                <el-table-column label="容量" width="60" align="center" prop="sku_volume"></el-table-column>
                <el-table-column label="销售价格" width="60" align="center"> 
                    <template slot-scope="scope"><el-input v-model="scope.row.sku_price"></el-input></template>
                </el-table-column>
                <el-table-column label="商品库存" width="60" align="center"> 
                    <template slot-scope="scope"><el-input v-model="scope.row.sku_stock"></el-input></template>
                </el-table-column>
                <el-table-column label="库存预警值" width="60" align="center"> 
                    <template slot-scope="scope"><el-input v-model="scope.row.sku_warningValue"></el-input></template>
                </el-table-column>
            </el-table>
        </el-dialog>
        <el-table ref="productTable"
                :data="mallList"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                v-loading="productLoad"
                border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="编号" width="100" align="center">
            <template slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column label="商品图片" width="120" align="center">
            <template slot-scope="scope"><img style="height: 80px" :src="scope.row.img"></template>
        </el-table-column>
        <el-table-column label="商品名称" align="center">
            <template slot-scope="scope">
                <p>{{scope.row.good_name}}</p>
                <p>品牌：{{scope.row.good_brand}}</p>
            </template>
        </el-table-column>
        <el-table-column label="价格/货号" width="120" align="center">
            <template slot-scope="scope">
                <p>价格：￥{{scope.row.price}}</p>
                <p>货号：{{scope.row.productNum}}</p>
            </template>
        </el-table-column>
        <el-table-column label="标签" width="140" align="center">
            <template slot-scope="scope">
                <p>上架：
                    <el-switch @change="handleGroundingChange(scope.$index, scope.row)" 
                    :active-value="1" :inactive-value="0" v-model="scope.row.grounding">
                    </el-switch>
                </p>
                <p>新品：
                    <el-switch
                        @change="handleNewProductChange(scope.$index, scope.row)"
                        :active-value="1"
                        :inactive-value="0"
                        v-model="scope.row.newProduct">
                    </el-switch>
                </p>
                <p>推荐：
                    <el-switch
                        @change="handleRecommendStatusChange(scope.$index, scope.row)"
                        :active-value="1"
                        :inactive-value="0"
                        v-model="scope.row.recommandStatus">
                    </el-switch>
                </p>
            </template>
        </el-table-column>
        <el-table-column label="SKU库存" width="100" align="center">
            <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="handleShowSkuEditDialog(scope.$index, scope.row)" circle></el-button>
            </template>
        </el-table-column>
        <el-table-column label="销量" width="100" align="center">
            <template slot-scope="scope">{{scope.row.sale}}</template>
        </el-table-column>
        <el-table-column label="审核状态" width="100" align="center">
            <template slot-scope="scope">
                <p>{{scope.row.verifyStatus?'已审核':'未审核'}}</p>
                <p>
                    <el-button
                        type="text"
                        @click="handleShowVerifyDetail(scope.$index, scope.row)">审核详情
                    </el-button>
                </p>
            </template>
        </el-table-column>
        <el-table-column label="操作" width="160" align="center">
            <template slot-scope="scope">
                <p>
                    <el-button
                        size="mini"
                        @click="handleShowProduct(scope.$index, scope.row)">查看
                    </el-button>
                    <el-button
                        size="mini"
                        @click="handleUpdateProduct(scope.$index, scope.row)">编辑
                    </el-button>
                </p>
                <p style="margin-top: 10px">
                    <el-button
                        size="mini"
                        @click="handleShowLog(scope.$index, scope.row)">日志
                    </el-button>
                    <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除
                    </el-button>
                </p>
            </template>
        </el-table-column>
      </el-table>
    </div>
</template>

